import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import red from '@material-ui/core/colors/red';
import orange from '@material-ui/core/colors/orange';
import green from '@material-ui/core/colors/green';

const styles = {
  root: {
    width: '100%',
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
};

let id = 0;
function createData(name, printer, priority, customer_id, DLC) {
  id += 1;
  return { id, name, printer, priority, customer_id, DLC };
}

const data = [
  createData('Brett Brinker', 'MiM', 1, 11, new Date().toLocaleString()),
  createData('Tommy Tucson', 'Fleet', 2, 8, new Date().toLocaleString()),
  createData('Tina Cupcake', '3rd Party', 4, 5, new Date().toLocaleString()),
  createData('Ginger Mayflower', 'MiM', 3, 3, new Date().toLocaleString()),
  createData('Larry SamAdams', 'MiM', 3, 9, new Date().toLocaleString()),
];

const tableStyle = (priority, defaultStyle = {}) => {
  if (priority <= 2)
        return {
            ...defaultStyle,
            backgroundColor: red[200],
        };
  if (priority == 3)
        return {
            ...defaultStyle,
            backgroundColor: orange[200],
        };
  if (priority >= 4)
        return {
            ...defaultStyle,
            backgroundColor: green[200],
        };
        return defaultStyle
}

function SimpleTable(props) {
  const { classes } = props;

  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell>ATC (Application to Contact)</TableCell>
            <TableCell align="right">Printer</TableCell>
            <TableCell align="right">Priority (g)</TableCell>
            <TableCell align="right">Customer (g)</TableCell>
            <TableCell align="right">Last Contact (g)</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map(n => (
            <TableRow key={n.id} style={tableStyle(n.priority)}>
              <TableCell component="th" scope="row">
                {n.name}
              </TableCell>
              <TableCell align="right">{n.printer}</TableCell>
              <TableCell align="right">{n.priority}</TableCell>
              <TableCell align="right">{n.customer_id}</TableCell>
              <TableCell align="right">{n.DLC}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Paper>
  );
}

SimpleTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SimpleTable);
