import React from 'react';
import PostIcon from '@material-ui/icons/Book';
import { Admin, Resource, ListGuesser, EditGuesser } from 'react-admin';
//import jsonServerProvider from 'ra-data-json-server';
import dataProvider from './dataProvider';
import posts from './posts/';
import UserCreate from './users/UserCreate';
import UserList from './users/UserList';
import UserEdit from './users/UserEdit';
import UserShow from './users/UserShow';
import UserIcon from '@material-ui/icons/Group';
import TagList from './tags/TagList';
import TagShow from './tags/TagShow';
import TagEdit from './tags/TagEdit';
import Dashboard from './dash/Dashboard';
import Layout from './Layout';
import authProvider from './authProvider';
import themeReducer from './themeReducer';
import lightTheme from './themes';
import customRoutes from './routes';
import PriTables from './album/PriTables';
import Checkout from './pricing/Checkout';
import Blog from './blog/Blog';
import { Route } from 'react-router';
import Album from './album/Album';
import Configuration from './configuration/Configuration';
import Segments from './segments/Segments';
import Wizard from './wizard/Wizard';
import Signup from './wizard/Signup';
import Charts from './wizard/Charts';
import Profile from './chat/Profile';
import { ReviewList, ReviewEdit, ReviewIcon } from './reviews';
//import users from './users/'
import {
    ProductList,
    ProductCreate,
    ProductEdit,
    ProductIcon,
} from './products';
import {
    CategoryList,
    CategoryEdit,
    CategoryIcon,
} from './categories';
import {
    CommandList,
    CommandEdit,
    CommandIcon
} from './commands';
import {
    VisitorList,
    VisitorEdit,
    VisitorCreate,
    VisitorIcon,
} from './visitors';
import Menu from './Menu';
import englishMessages from './i18n/en';
//import UserProfile from './mimdash/userProfile'
const i18nProvider = locale => {
    return englishMessages
}


const App = () => (
    <Admin
        dataProvider={dataProvider}
        authProvider={authProvider}
        dashboard={Dashboard}
        customReducers={{ theme: themeReducer }}
        appLayout={Layout}
        theme={lightTheme}
        customRoutes={[
            <Route exact path='/pricing' component={Checkout} />,
            <Route exact path="/configuration" component={Configuration} />,
            <Route exact path="/segments" component={Segments} />,
            <Route exact path='/album' component={Album} />,
            <Route exact path='/pritables' component={PriTables} />,
            <Route exact path='/blog' component={Blog} />,
            <Route exact path='/profile' component={Profile} />,
            <Route exact path='/wizard' component={Wizard} />,
            <Route exact path='/signup' component={Signup} />,
            <Route exact path='/charts' component={Charts} /> ]}
        menu={Menu}
        i18nProvider={i18nProvider}
        locale="en"
    >
        <Resource
            name="customers"
            list={VisitorList}
            edit={VisitorEdit}
            create={VisitorCreate}
            icon={VisitorIcon}
        />
        <Resource
            name="posts"
            {...posts}
        />
        <Resource
            name="commands"
            list={CommandList}
            edit={CommandEdit}
            icon={CommandIcon}
            options={{ label: 'Orders' }}
        />
        <Resource name="users" list={UserList} edit={UserEdit} create={UserCreate} icon={UserIcon} show={UserShow} />
        <Resource name="comments" list={ListGuesser} />
        <Resource name="products" list={ProductList} create={ProductCreate} edit={ProductEdit} icon={ProductIcon} />
        <Resource
                    name="categories"
                    list={CategoryList}
                    edit={CategoryEdit}
                    icon={CategoryIcon}
                />
        <Resource name="reviews" list={ReviewList} edit={ReviewEdit} icon={ReviewIcon} />
        <Resource name="tags" list={TagList} edit={TagEdit} show={TagShow} />

    </Admin>
);
export default App;
